import React, { Suspense, lazy } from 'react';
import { Tabs, Tab } from 'react-bootstrap';
import Spinner from '../../../../components/spinner/spinner';
// import CreditCard from './components/creditCard';
// import MutualFunds from './components/mutualFund';
// import PersonalLoans from './components/personalLoans';
// import HealthInsurance from './components/healthInsurance';
// import TermInsurance from './components/termInsurance';
const CreditCard = lazy(() => import('./components/creditCard'));
const MutualFunds = lazy(() => import('./components/mutualFund'));
const PersonalLoans = lazy(() => import('./components/personalLoans'));
// const HealthInsurance = lazy(() => import('./components/healthInsurance'));
// const LifeInsurance = lazy(() => import('./components/lifeInsurance'));
// const MotorInsurance = lazy(() => import('./components/motorInsurance'));

export default class TrendingProducts extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isPersonal: true,
            isCredit: true,
            key: 'mutualFund'
        }
    }

    showTabs = (key, value) => {
        this.setState({ [key]: value });
    }

    setTab = (key) => {
        this.setState({ key });
        let val = '';
        if(key === 'mutualFund'){
            val = 'Mutual_Funds'
        } else if (key === 'creditCard'){
            val = 'Credit_Card'
        } else if (key === 'personalLoans') {
            val = 'Personal_Loan'
        }
        window.dataLayer.push({
            'event': 'Trending_Offers_Section',
            'eventCategory': 'Trending_Offers_Section_click',
            'eventAction': val, // {{dynamic}} contains offer name i.e. Mutual_Funds, Credit_Card, Personal_Loan, View_all
            'eventLabel': window.location.href, // {{dynamic}} contains the url of page to which user is redirected i.e stage.onebajaj.capital/about
        });
    }

    render() {
        const { isCredit, isPersonal, key } = this.state;
        return (
            <section className="trendingcard  pt40 pb40">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12 sec-heading">
                            <h2> Trending Offers</h2>
                            <span className="d-none d-sm-block"> We know just how important it is to you that we answer all your financial needs.
                            That's why we look at what our users have been recommending to get you the best offers of the moment.</span>
                        </div>

                        <div className="col-lg-12">
                            <Suspense fallback={<Spinner />}>
                                <Tabs activeKey={key} onSelect={this.setTab}
                                    id="pills-tab" className="nav nav-pills mb-3 justify-content-center">
                                    <Tab eventKey="mutualFund" title="Mutual Funds" className="nav-item">
                                        <MutualFunds />
                                    </Tab>
                                    {isCredit && <Tab eventKey="creditCard" title="Credit Cards" className="nav-item">
                                        <CreditCard showTabs={this.showTabs} />
                                    </Tab>}
                                    {isPersonal && <Tab eventKey="personalLoans" title="Personal Loans" className="nav-item">
                                        <PersonalLoans showTabs={this.showTabs} />
                                    </Tab>}
                                    {/* <Tab eventKey="healthInsurance" title="Health Insurance" className="nav-item">
                                        <HealthInsurance />
                                    </Tab>
                                    <Tab eventKey="lifeInsurance" title="Life Insurance" className="nav-item">
                                        <LifeInsurance />
                                    </Tab>
                                    <Tab eventKey="motorInsurance" title="Motor Insurance" className="nav-item">
                                        <MotorInsurance />
                                    </Tab> */}
                                </Tabs>
                            </Suspense>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}